.search-bar {
  display: flex;
  justify-content: flex-start;
  background-color: transparent;
  border-radius: 45px;
  height: 3vh;
  padding: 1vw;
  
  position: fixed;
  top: 2vh;
  right: 30vw;

  width: 40vw;
  border: 1px solid white;
}

.search-input {
  flex-grow: 1;
  border: none;
  
  background-color: transparent;
  
  caret-color: white;
  color: white;

  font-size: calc(1vw + 1vh);
  
  padding: calc(0.5vw + 0.5vh);
}

.search-input::placeholder {
  color: rgba(255,255,255,0.895);
}

.search-input:focus {
  outline: none;
}

.search-button {
  background: none;
  border: none;
}

/* Adjust styles for smaller screens */
@media (max-width:600px) {
    .search-bar {
        right: calc(5vw + env(safe-area-inset-right)); /* Adjust the right position to maintain responsiveness */
    }
}
