@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');

/* Sidebar styles */
.sidebar {
  background-color: #292929;
  color: white;
  width: 210px;
  height: 100vh;
  padding: 2vw;
  position: fixed;
  top: -1px;
  border-radius: 0px 32px 32px 0px;
}

/* Search input styles */
.search-input {
  width: calc(100% - 40px);
  padding: 10px; 
}

/* Button styles */
.pair-address,
.token-address {
  background-color: #292929;
  color: white;
  width: 220px;
  height: auto; /* Allow height to adjust based on content */
  padding: 2vw;
  
  font-family: 'Work Sans', sans-serif;
  font-size: 20px; /* Font size */
  
  line-height: normal; /* Line height */
}

/* Reset any previous float or alignment rules */
.title + .pair-address,
.title + .token-address {
    float: none;
    clear: both;
    text-align: left;
}

/* Hover and focus styles for buttons */
.pair-address:hover,
.token-address:hover,
.pair-address:focus,
.token-address:focus {
    background-color: #F30050; 
}

/* Title styles */
.title {
    width: auto; /* Allow width to adjust based on content */
    height: auto; /* Allow height to adjust based on content */
    
    font-family: 'Pacifico', cursive;
    font-size: 32px; /* Font size */

    line-height: normal;

    color: rgba(255,255,255,0.70);
    
    padding-left:60px; /* Add some left padding for the logo */

    background-image:url('/src/images/Vector.png');
    background-repeat:no-repeat; 
    background-size:auto; /* Adjust based on your logo's dimensions */
    background-position:center left; /* Adjust as needed */

    margin-bottom :40px; /* Margin at the bottom of the title */
}

.Vector {
    fill:#FFF; /* Set the fill color of the logo */
}

/* No margin between title and buttons */
.title + .pair-address,
.title + .token-address {
    margin-top :0; 
}

/* Media query for smaller screens */
@media (max-width :600px) {
    
    .pair-address,
    .token-address {
        width :100%;
        padding :10px; /* Adjust padding for smaller screens */
        font-size :16px; /* Adjust font size for smaller screens */
        height :auto; /* Allow height to adjust based on content */
    }

    .title {
        font-size :24px; /* Adjust font size for smaller screens */
        margin-bottom :20px; /* Adjust margin for smaller screens */
        padding-left :40px; /* Adjust padding for smaller screens */
        width:auto; /* Allow width to adjust based on content */
        height:auto; /* Allow height to adjust based on content */
    }

    /* Reset margin between title and buttons for smaller screens */
    .title + .pair-address,
    .title + .token-address {
        margin-top :10px; /* Add margin between title and buttons */
    }
}
.pair-address,
.token-address {
  position:sticky;  
  top: 10;
    
  left: 0;
}
